// src/components/empresa/tabs/FaqTab.tsx
"use client"

import { FC, useState, HTMLAttributes } from 'react'
import { FaqItem } from '@/domain/empresa'

interface FaqTabProps {
  items?: FaqItem[]
}

interface AccordionItemProps extends HTMLAttributes<HTMLDivElement> {
  question: string
  answerHtml: string
  isOpen: boolean
  onToggle: () => void
}

const AccordionItem: FC<AccordionItemProps> = ({
  question,
  answerHtml,
  isOpen,
  onToggle,
  ...rest
}) => (
  <div {...rest} className={`border-b ${isOpen ? 'active' : ''}`}>
    <button
      className="w-full flex justify-between items-center p-6 hover:bg-gray-50"
      onClick={onToggle}
    >
      <h3 className="font-medium">{question}</h3>
      <i
        className={`fas fa-chevron-down transition-transform ${
          isOpen ? 'rotate-180' : ''
        }`}
      />
    </button>
    {isOpen && (
      <div
        className="px-6 pb-6 text-gray-700"
        dangerouslySetInnerHTML={{ __html: answerHtml }}
      />
    )}
  </div>
)

const FaqTab: FC<FaqTabProps> = ({ items = [] }) => {
  const [openIndex, setOpenIndex] = useState<number>(-1)

  const handleToggle = (index: number) => {
    setOpenIndex(openIndex === index ? -1 : index)
  }

  return (
    <section className="space-y-6">
      <h2 className="text-2xl font-bold text-blue-800">Perguntas Frequentes</h2>
      <div className="bg-white rounded-xl shadow-sm border border-gray-200">
        {items.map((faq, idx) => (
          <AccordionItem
            key={idx}
            question={faq.q}
            answerHtml={faq.a}
            isOpen={openIndex === idx}
            onToggle={() => handleToggle(idx)}
          />
        ))}
      </div>
    </section>
  )
}

export default FaqTab
