'use client'

import { FC, useState } from 'react'
import { Review } from '@/domain/empresa'

interface ReviewsTabProps {
  reviews?: Review[]
}

const ReviewsTab: FC<ReviewsTabProps> = ({ reviews = [] }) => {
  const [visibleCount, setVisibleCount] = useState(4)

  return (
    <section className="space-y-6">
      <h2 className="text-2xl font-bold text-blue-800">Avaliações de Clientes</h2>

      <div className="grid md:grid-cols-2 gap-6">
        {reviews.slice(0, visibleCount).map((r, idx) => (
          <div
            key={`${r.name}-${idx}`}
            className="bg-white p-6 rounded-xl shadow-sm border border-gray-200"
          >
            <div className="flex justify-between mb-3">
              <div className="flex items-center">
                <div className="w-10 h-10 rounded-full bg-gray-200 flex items-center justify-center mr-3">
                  <i className="fas fa-user text-gray-600" />
                </div>
                <div>
                  <h4 className="font-medium">{r.name}</h4>
                  <div className="flex items-center text-sm text-gray-500">
                    <i className="fas fa-map-marker-alt mr-1" />{r.city}
                  </div>
                </div>
              </div>
              <div className="text-sm text-gray-500">{r.time}</div>
            </div>

            <div className="flex items-center mb-3">
              <div className="flex mr-2 text-yellow-400">
                {Array.from({ length: 5 }).map((_, i) =>
                  i < Math.floor(r.stars) ? (
                    <i key={i} className="fas fa-star" />
                  ) : i === Math.floor(r.stars) && r.stars % 1 !== 0 ? (
                    <i key={i} className="fas fa-star-half-alt" />
                  ) : (
                    <i key={i} className="far fa-star" />
                  )
                )}
              </div>
              <span className="text-sm font-medium">{r.type}</span>
            </div>

            <h5 className="font-medium mb-2">{r.title}</h5>
            <p className="text-gray-700 mb-4">{r.body}</p>

            <div className="flex flex-wrap gap-2 mb-4">
              {r.tags.map((tag, i) => (
                <span
                  key={i}
                  className="bg-gray-100 text-gray-800 text-xs px-2 py-1 rounded"
                >
                  {tag}
                </span>
              ))}
            </div>

            {r.reply && (
              <div className="bg-blue-50 p-3 rounded-lg border border-blue-100">
                <div className="flex items-start gap-2">
                  <div className="mt-1">
                    <i className="fas fa-check-circle text-blue-500" />
                  </div>
                  <div>
                    <div className="font-medium text-blue-800 mb-1">
                      Resposta da {r.reply.author}
                    </div>
                    <p className="text-blue-700 text-sm">{r.reply.text}</p>
                  </div>
                </div>
              </div>
            )}
          </div>
        ))}
      </div>

      {visibleCount < reviews.length && (
        <div className="text-center mt-4">
          <button
            onClick={() => setVisibleCount((c) => c + 4)}
            className="border border-blue-600 text-blue-600 hover:bg-blue-50 px-6 py-3 rounded-lg font-medium"
          >
            <i className="fas fa-chevron-down mr-2" /> Carregar mais avaliações
          </button>
        </div>
      )}
    </section>
  )
}

export default ReviewsTab
